//This is just a random walker and nothing else.

let canvasDiv = document.getElementById('canvasContainer');
let divWidth = canvasDiv.offsetWidth;
let divHeight = canvasDiv.offsetHeight;

let x1, x2, y1, y2;
let s = 10;

function setup() {
    createCanvas(divWidth, divHeight).parent("canvasContainer");
    background('#FDE033');
    x1 = 0;
    y1 = 0;
    x2 = x1;
    y2 = y1;
}

function draw() {
    //noStroke();
    stroke('#FDE033');
    fill(0);
    strokeWeight(2);
    rect(x1, y1, 10);

    let ran = random();
    x1 = x2;
    y1 = y2;
    if (ran < 0.3) x2 += s; //move to right
    else if (ran < 0.5) x2 -= s; //move to left
    else if (ran < 0.75) y2 -= s; //move up
    else y2 += s; //move down

    if (x2 > width) {
        x2 = 0
    }
    if (y2 > height) {
        y2 = 0
    }
    if (x2 < 0) {
        x2 = width
    }
    if (y2 < 0) {
        y2 = height
    }
}


function windowResized() {
    divWidth = canvasDiv.offsetWidth;
    divHeight = canvasDiv.offsetHeight;
    resizeCanvas(divWidth, divHeight);
}